<template>
    <div class="swiper-wrap" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd">
      <ul ref="swiperDom" style="transform: translate3d(0, 0, 0)">
          <slot/>
      </ul>
      <div class="showPagenation" v-if="showPagenation">
        <span v-for="(item,$index) in swiper.slideLength"
              v-bind:class="{active:-(swiper.index)===$index}">
        </span>
      </div>
    </div>
</template>

<script>
  export default {
    name: 'swiper',
    data () {
      return {
        swiper:{
          start:0,
          move:0,
          end:0,
          index:0,
          isSlideing:true,
          transformX:0,
          screenWidth:0,
          wrapWidth:0,
          translateX:0,
          domTransition:0,
          swiperCondition:0.4,
          slideLength:1,
          autoTimer:''
        }
      }
    },
    props:{
      showPagenation:{
        default:true
      },
      interval:{
        default:3000
      },
      autoplay:{
        default:false
      }
    },
    mounted:function () {
      this.init();
    },
    methods:{
      init:function () {
        this.getSlideLength();
        this.swiper.screenWidth = window.screen.width;
        this.swiper.wrapWidth = window.screen.width*(this.swiper.slideLength + 1);
        this.$refs.swiperDom.style.width = 100*(this.swiper.slideLength+2)+'%';
        this.$refs.swiperDom.style.transform = "translate3d(" + -this.swiper.screenWidth +"px, 0, 0)";
        for(let i=0;i<this.swiper.slideLength;i++){
          this.$refs.swiperDom.getElementsByTagName('li')[i].style.width = 100/(this.swiper.slideLength+2)+'%';
        }
        this.cloneDom();
        if(this.showPagenation){
          this.autoSlide()
        }
      },
      touchStart:function (e) {
        if(this.swiper.isSlideing){
          clearInterval(this.autoTimer);
          this.swiper.start = e.touches[0].clientX;
          this.swiper.transformX = this.getTranslate();
        }
      },
      touchMove:function (e) {
        var swiperWrap = this.$refs.swiperDom;
        this.swiper.move = e.touches[0].clientX - this.swiper.start;
        this.swiper.translateX = parseInt(this.swiper.move) + parseInt(this.swiper.transformX);
        swiperWrap.style.transition = '0s';
        swiperWrap.style.transform = "translate3d(" + this.swiper.translateX +"px, 0, 0)";
      },
      touchEnd:function () {
        this.autoSlide();
        if(this.swiper.move<0){
          if(this.swiper.translateX%this.swiper.screenWidth<-this.swiper.screenWidth*0.2){
            this.swiper.index--;
            this.doSlide()
          }
        }else {
          if(this.swiper.translateX%this.swiper.screenWidth>-this.swiper.screenWidth*0.8){
            this.swiper.index++;
            this.doSlide()
          }
        }
        if(this.getTranslate()>0){
          this.swiper.index = 1
        }
        else if(this.getTranslate() <= -this.swiper.wrapWidth){
          setTimeout(()=>{
            this.$refs.swiperDom.style.transition = '0s';
            this.$refs.swiperDom.style.transform = "translate3d(" + -(this.swiper.screenWidth) +"px, 0, 0)";
            this.swiper.index = 0;
          },250);
        }
      },
      cloneDom:function () {
        var firstDom = this.$refs.swiperDom.getElementsByTagName('li')[0].cloneNode(true);
        var lastDom = this.$refs.swiperDom.getElementsByTagName('li')[this.swiper.slideLength-1].cloneNode(true);
        this.$refs.swiperDom.insertBefore(lastDom,this.$refs.swiperDom.getElementsByTagName('li')[0]);
        this.$refs.swiperDom.appendChild(firstDom);
      },
      getTranslate:function () {
        var X = this.$refs.swiperDom.style.transform;
        X = X.substring(12).match(/(\S*)px/);
        return Number(X[1])
      },
      getSlideLength:function () {
        this.swiper.slideLength = this.$refs.swiperDom.getElementsByTagName('li').length
      },
      doSlide:function () {
        var swiperWrap = this.$refs.swiperDom;
        swiperWrap.style.transition = '.25s';
        swiperWrap.style.transform = "translate3d(" + (this.swiper.screenWidth*(this.swiper.index-1)) +"px, 0, 0)";
      },
      autoSlide:function () {
        if(this.autoplay){
          this.autoTimer = setInterval(()=>{
            this.swiper.index--;
            if(this.swiper.index<=-this.swiper.slideLength){
              setTimeout(()=>{
                this.$refs.swiperDom.style.transition = '0s';
                this.$refs.swiperDom.style.transform = "translate3d(" + -(this.swiper.screenWidth) +"px, 0, 0)";
                this.swiper.index = 0;
              },250);
            }
            this.doSlide()
          },this.interval);
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .swiper-wrap{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
  }
  .swiper-wrap ul{
    height: 100%;
    overflow: hidden;
  }
  .swiper-wrap li{
    height: 100%;
    float: left;
    overflow: hidden;
  }
  .showPagenation{
    position: absolute;
    bottom: 8px;
    width: 100%;
    text-align: center;
    background: 0 0;
  }
  .showPagenation span{
    display: inline-block;
    width: 8px;
    height: 8px;
    margin: 1px 7px;
    cursor: pointer;
    border-radius: 100%;
    background: #aaa;
    transition: .5s;
  }
  .showPagenation span.active{
    background: #0fc37c;
  }
</style>



